const Summary = props => {
  const {total = []} = props;
  return (
    <SummaryWrapper>
      {
        total.map(item => (
          <div key={item.label}>
            <dt>{item.label}</dt>
            <dd>{item.value}</dd>
          </div>
        ))
      }
    </SummaryWrapper>
  )
};

