import { omitBy, isNil, isEmpty } from 'lodash';

const formatCompareData = data => {
  const { label = [], target, value = [] } = data || {};
  const typeCurrent = [];
  const typeRemain = [];

  label.forEach((item, i) => {
    typeCurrent.push({
      type: 'current',
      label: item,
      value: value[i].current,
      target: target ? target[i] : null
    });
    typeRemain.push({
      type: 'remain',
      label: item,
      value: value[i].remain,
      target: target ? target[i] : null
    });
  })
  const arr = typeRemain.concat(typeCurrent);
  const result = arr.map(item => omitBy(item, isNil));
  return isEmpty(result) ? null : result;
}

export { formatCompareData }
