import React from "react";
import {
  Chart,
  Geom,
  Axis,
  Tooltip
} from "bizcharts";
import {isEmpty} from 'lodash';
import DataSet from "@antv/data-set";

const ChartBarPercent = props => {
  const data = props.data || [];
  const ds = new DataSet();
  const dv = ds
    .createView()
    .source(data);
  return !isEmpty(data) ? (
    <Chart width={props.width} height={props.height} data={dv} forceFit padding={props.padding || [10, 10, 30, 60]}>
      <Axis name="label" tickLine={null} />
      <Axis name="value" tickLine={null} grid={
        {
          lineStyle: {
            stroke: '#033154',
            lineDash: [1, 0]
          }
        }
      } />
      <Tooltip />
      <Geom
        type="intervalStack"
        position="label*value"
        color={props.color ? props.color : 'type'}
      />
    </Chart>
  ) : null;
}

export default ChartBarPercent;