import React from "react";
import {isEmpty} from 'lodash';
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Legend
} from "bizcharts";
import DataSet from "@antv/data-set";

const ChartBar = props => {
  const {data = [], fields=[], color} = props;
  
  return !isEmpty(data) ? (
    <Chart width={props.width} height={props.height} data={data} forceFit padding={[10, 10, 60, 40]}>
      <Axis name="label" tickLine={null} />
      <Axis name="value" tickLine={null} grid={
        {
          lineStyle: {
            stroke: '#033154',
            lineDash: [1, 0]
          }
        }
      }/>
      <Legend />
      <Tooltip
        crosshairs={{
          type: "y"
        }}
      />
      <Geom
        type="interval"
        position="label*value"
        color={color || ["type", ["#1BA3FC", "#00FFE0"]]}
        adjust={[
          {
            type: "dodge",
            marginRatio: 1 / 32
          }
        ]}
      />
    </Chart>
  ) : null;
}

export default ChartBar;