import React from "react";
import styled from 'styled-components';
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Guide
} from "bizcharts";
import DataSet from "@antv/data-set";

const Title = styled.h4`
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  color: #fff;
`;

const ChartPie = props => {
  const { DataView } = DataSet;
    const data = [
      {
        name: "剩余",
        count: 21
      },
      {
        name: "回款",
        count: 40
      }
    ];
    const dv = new DataView();
    dv.source(data).transform({
      type: "percent",
      field: "count",
      dimension: "name",
      as: "percent"
    });
    const cols = {
      percent: {
        formatter: val => {
          val = val * 100 + "%";
          return val;
        }
      }
    };
    return (
      <Chart
        width={props.width / 4}
        height={props.height + 20}
        data={dv}
        scale={cols}
        padding={[10, 0, 10, 0]}
        forceFit
      >
        <Coord type="theta" radius={0.75} />
        <Axis name="percent" visible={false} />
        <Tooltip
          showTitle={false}
          itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
        />
        <Geom
          type="intervalStack"
          position="percent"
          color={props.color || 'name'}
          tooltip={[
            "name*percent",
            (name, percent) => {
              percent = percent * 100 + "%";
              return {
                name,
                value: percent
              };
            }
          ]}
        >
          <Label
            content="count"
            offset={0}
            textStyle={{
              rotate: 0,
              textAlign: "center",
              shadowBlur: 2,
              fill: "#dedede",
              textBaseline: "bottom"
            }}
            formatter={(text, item, index)=>{
              return text + '万'
            }}
          />
        </Geom>
        {props.title && <Guide><Title>{props.title}</Title></Guide>}
      </Chart>
    );
}

export default ChartPie;
