import React from 'react';
import styled from 'styled-components';
import {COLOR} from '../helper/const';

const Tooltip = styled.div`
  display: none;
  position: absolute;
  padding: 15px;
  transform: translate(-50px, -150%);
  z-index: 999;
  font-size: 12px;
  background-color: rgba(255, 255, 255, .9);
  color: #333;
  border-radius: 5px;
  box-shadow: 0 0 3px #fff;
  strong {
    margin-right: 10px;
    font-weight: normal;
    white-space: nowrap;
  }
`;

const ProgressBar = styled.div`
  position: relative;
  height: ${props => props.height && props.height};
  background-color: ${props => props.bgColor && props.bgColor};
  &:hover ${Tooltip} {
    display: block;
  }
`;

const Bar = styled.div`
  width: ${props => props.value ? props.value + '%' : 0};
  height: 100%;
  background-color: ${props => props.barColor && props.barColor};
`;

const Label = styled.span`
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  line-height: 2rem;
  text-align: center;
  font-size: 1.2rem;
  color: #333;
`;

const Progress = props => {
  const {value, color, showProgress = false, label, height='2rem'} = props;
  const width = typeof(value) === 'number' ? value : parseInt(value, 10);
  const [barColor, bgColor] = typeof(color) === 'object' ? color : [COLOR.navColor, '#dedede'];
  return (
    <ProgressBar height={height} bgColor={bgColor}>
      <Bar value={width} barColor={barColor} />
      {showProgress && <Label>{width}%</Label>}
      <Tooltip height={height}>
        <strong>{label}</strong>
        <span>{width}%</span>
      </Tooltip>
    </ProgressBar>
  )
}

export default Progress;